











import { PostModel } from "@/service/interfaces";
import Vue from "vue";
import Component from "vue-class-component";
import PostMetaComponent from "@/components/forum/PostMeta.vue";
import UserAvatar from "@/components/forum/UserAvatar.vue";

@Component({
  props: ["post"],
  components: {
    PostMetaComponent,
    UserAvatar,
  },
})
export default class PostPreviewComponent extends Vue {
  post!: PostModel;

  title = "No title";
  url = "/";

  mounted(): void {
    if (this.post.title) this.title = this.post.title;
    if (this.post.relativeUrl) this.url = this.post.relativeUrl;
  }
}
