



























import Vue from "vue";
import Component from "vue-class-component";
import { PostModel, RequestData } from "@/service/interfaces";
import { AppService } from "@/service/app.service";
import { ApiService } from "@/service/api.service";
import PostPreviewComponent from "@/components/forum/PostPreview.vue";

@Component({
  components: {
    PostPreviewComponent,
  },
})
export default class PostList extends Vue {
  posts: Array<PostModel> = [];
  category = "";
  app = AppService.instance;
  public api: ApiService = ApiService.instance;

  loadingPosts = false;

  options: RequestData = {};

  mounted(): void {
    this.options.category = this.$route.params.category;
    this.options.userIdx = this.$route.params.userIdx;
    this.loadPosts();
  }

  async loadPosts(): Promise<void> {
    console.log(this.options);
    this.loadingPosts = true;
    try {
      this.posts = await this.app.api.postSearch(this.options);
      // console.log(this.posts);
      this.loadingPosts = false;
    } catch (error) {
      this.app.error(error);
      this.loadingPosts = false;
    }
  }
}
